export default {
	onShareAppMessage(){},
	data() {
		return {
			tc_index: -1,
			bottom_Alldata: [
				{
					name: '趣味电影数据之上半年每分钟最赚钱影片',
					lay: '来源：拓普数据实验室',
					time: '2019-09-03',
					image1:'',
					image2:'',
					image3:''
				},
				{
					name: '趣味电影数据之上半年每分钟最赚钱影片',
					lay: '来源：拓普数据实验室',
					time: '2019-09-03',
					image1:'',
					image2:'',
					image3:''
				},
				{
					name: '趣味电影数据之上半年每分钟最赚钱影片',
					lay: '来源：拓普数据实验室',
					time: '2019-09-03',
					image1:'',
					image2:'',
					image3:''
				},
				{
					name: '趣味电影数据之上半年每分钟最赚钱影片',
					lay: '来源：拓普数据实验室',
					time: '2019-09-03',
					image1:'',
					image2:'',
					image3:''
				},
				{
					name: '趣味电影数据之上半年每分钟最赚钱影片',
					lay: '来源：拓普数据实验室',
					time: '2019-09-03',
					image1:'',
					image2:'',
					image3:''
				},
				{
					name: '趣味电影数据之上半年每分钟最赚钱影片',
					lay: '来源：拓普数据实验室',
					time: '2019-09-03',
					image1:'',
					image2:'',
					image3:''
				},
				{
					name: '趣味电影数据之上半年每分钟最赚钱影片',
					lay: '来源：拓普数据实验室',
					time: '2019-09-03',
					image1:'',
					image2:'',
					image3:''
				},
				{
					name: '趣味电影数据之上半年每分钟最赚钱影片',
					lay: '来源：拓普数据实验室',
					time: '2019-09-03',
					image1:'',
					image2:'',
					image3:''
				}
			],
			header_tab_data: ['关注', '推荐', '线索', '信息', '爆料', '排行榜', '资讯'],
			header_tab_index: 2,
			page_index: 0 // 当前页码
		};
	},
	onReachBottom() {
		// console.log('触发底部');
		var page = this.page_index;
		var url = 'https://db.topcdb.com/topprogram/get-report?page=' + page;
		page++;
		this.page_index = page;
		this.db_index(url, 1);
	},
	onLoad(data) {
		var page = this.page_index;

		var Br = data.Br;
		if (Br) {
			var res = data.res;
			let id = res.id;
			let brd = res.brd;
			let district = res.district;
			let city = res.city;
			console.log(1);

			var url = 'https://db.topcdb.com/topprogram/get-report?page=' + page + '&city=' + city + '&district=' + district + '&brd=' + brd;
			page++;
			this.page_index = page;
			this.db_index(url);
		} else {
			var url = 'https://db.topcdb.com/topprogram/get-report?page=' + page;
			page++;
			this.page_index = page;
			this.db_index(url);
		}
	},
	methods: {
		// 跳转详情页
		tz_click(id, brd, district, city) {
			if (brd == null) {
				brd = '';
			}
			if (district == null) {
				district = '';
			}
			if (city == null) {
				city = '';
			}
			// console.log(brd);
			// console.log(district);
			// console.log(city);
			// console.log(id);
			uni.navigateTo({
				url: './list/list?id=' + id + '&brd=' + brd + '&district=' + district + '&city=' + city
			});
		},
		// 发送请求
		db_index(url, index) {
			uni.showLoading({
				title: '数据加载中'
			});
			uni.request({
				url
			}).then(dd => {
				let [errer, ra] = dd;
				// console.log(ra)
				var res = [...ra.data];
				console.log(res);
				res.forEach(item => {
					item.updated = item.updated.slice(0, 10);
					for (let i in item.img) {
						item.image = item.img[i];
					}
				});
				if (index == 1) {
					const data = [...this.bottom_Alldata];
					res.forEach(item => {
						data.push(item);
					});
					this.bottom_Alldata = data;
					uni.hideLoading();
					return false;
				}

				this.bottom_Alldata = res;
				uni.hideLoading();
			});
		},
		tc_click_tap(i) {
			// if (i == 1) {
			//
			// }else if(i == 2) {
			//
			// }else if(i == 3) {
			//
			// } else if(i == 4){
			//
			// }
			this.tc_index = -1;
		},
		tc_click(id) {
			this.tc_index = id;
		},
		header_tab_click(i) {
			this.header_tab_index = i;
		}
	}
};
